import React from 'react';
import Card from './Card.js';

const CardList = ({robots}) => {
	return (
		<div>
			{
				robots.map((user,i) => {
					return (
						<Card 
							key={i} 
							id={robots[i].id} 
							category={robots[i].category} 
							branch={robots[i].branch}
						/>
					);
				})
			}
		</div>
	);
}

export default CardList;