import React, { Component } from 'react';
import CardList from '../components/CardList.js';
import SearchBox from '../components/SearchBox.js';
import {robots} from '../components/robots.js';
import './App.css';
import Scroll from '../components/Scroll.js';
import Navigation from '../components/Navigation.js';


class App extends Component {
	constructor() {
		super()
		this.state = {
			robots: robots,
			searchfield: ''
		}
	}

	onSearchChange = (event) => {
		this.setState({searchfield: event.target.value })
	}

	render() {
		const {robots,searchfield} = this.state;
		const filteredRobots = robots.filter(robot => {
			return robot.category.toLowerCase().includes(searchfield.toLowerCase())
			||robot.branch.toLowerCase().includes(searchfield.toLowerCase());
		})
		if (!robots.length) {
			return <h1>Loading</h1>


			/*Подумать что показывать, когда поиск ничего не найдет
		} else if (filteredRobots.length === 0) {
			return 	(
				<div className='tc'>
					<h1>iCodeYourIdea</h1>
					<p>Давно мечтаешь о красивом крутом быстром сайте, но не знаешь у кого спросить?</p>
					<SearchBox searchChange={this.onSearchChange} />
					<Scroll>
						<CardList robots={robots} />
					</Scroll>
				</div>
			);*/
		} else if (!filteredRobots.length) {
			return 	(
				<div style={{textAlign:'center', margin:'0', padding: '0', fontFamily:'sans-serif', background:'linear-gradient(to right, rgba(255,15,0,1), rgba(0,22,255,1))'}}>
					<Navigation />
					<h1>iCodeYourIdea</h1>
					<p>Давно мечтаешь о красивом крутом быстром сайте, но не знаешь у кого спросить?</p>
					<SearchBox searchChange={this.onSearchChange} />
					<Scroll>
						<h1>Подумать что показывать, когда поиск ничего не найдет</h1>
					</Scroll>
				</div>
			);} else {
			return (
				<div style={{textAlign:'center', margin:'0', padding: '0', fontFamily:'sans-serif', background:'linear-gradient(to right, rgba(255,15,0,1), rgba(0,22,255,1))'}}>
					<Navigation />
					<h1>iCodeYourIdea</h1>
					<p>Давно мечтаешь о красивом крутом быстром сайте, но не знаешь у кого спросить?</p>
					<SearchBox searchChange={this.onSearchChange} />
					<Scroll>
						<CardList robots={filteredRobots} />
					</Scroll>
				</div>
			);
		}
	}
}
export default App;
